import request from '@/utils/request'
export const getList = (params) => request({
  url: '/api/blade-visual/visual/list',
  method: 'get',
  params: params
});

export const copyObj = (id) => request({
  url: '/api/blade-visual/visual/copy',
  method: 'post',
  params: {
    id: id
  }
});

export const getCategory = (params) => request({
  url: '/api/blade-visual/category/list',
  method: 'get',
  params: params
});

export const getObj = (id) => request({
  url: '/api/blade-visual/visual/detail',
  method: 'get',
  params: {
    id
  }
});
export const uploadImg = (file,type) => request({
  url: '/api/blade-visual/visual/put-file/'+type,
  method: 'post',
  data: 'file=' + file,
  headers: { "Content-Type": "multipart/form-data" }
});

export const addObj = (data) => request({
  url: '/api/blade-visual/visual/save',
  method: 'post',
  data: {
    visual: {
      password: data.password,
      category: data.category,
      status: data.status,
      title: data.title,
    },
    config: {
      detail: JSON.stringify({
        name: data.title,
        width: data.width,
        height: data.height,
        scale: 1,
        backgroundImage: '/api/file/VisusalImg/bg/bg1.png',
        url: '',
        mark: {},
        gradeShow: false,
        gradeLen: 30,
      }),
      component: '[]'
    },
  }
});

export const updateComponent = (data) => request({
  url: '/api/blade-visual/visual/update',
  method: 'post',
  data: data
});

export const updateObj = (data) => request({
  url: '/api/blade-visual/visual/update',
  method: 'post',
  data: {
    "visual": {
      "id": data.id,
      "password": data.password,
      "category": data.category,
      "status": data.status,
      "title": data.title,
    }
  }
});

export const delObj = (id) => request({
  url: '/api/blade-visual/visual/remove',
  method: 'post',
  params: {
    ids: id
  }
});

export const getImgList = (type) => request({
  url: `/api/blade-visual/visual/${type}`,
  method: 'get'
});